.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  min-height: 58px;
  padding: 10px 45px;
  font-size: 21px;
  line-height: 26px;
  font-style: normal;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  background-color: @background_colorful;
  color: @page-background;
  box-sizing: border-box;
  outline: none;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: @button-hover;
  }

  &:active {
    color: @button-active-text;
    background-color: @button-active;
  }
}

.button_disabled {
  background-color: @border;

  &:hover,
  &:focus {
    background-color: @border;
  }

  &:active {
    color: @page-background;
    background-color: @border;
  }
}
