.modal-error {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 320px;
  height: 280px;
  padding: 8px 20px 35px;
  box-sizing: border-box;
  background-color: @page-background;
  box-shadow: 0 7px 15px @modal-shadow;
  z-index: 6;
}

.modal-error .text_keynote {
  padding-bottom: 30px;
}

.modal-error__button-ok {
  width: 180px;
}

@media (min-width: @tablet-width) {
  .modal-error {
    width: 400px;
    height: 347px;
    padding: 50px 46px 49px;
  }

  .modal-error__text-container {
    padding: 0;
  }

  .modal-error__text_keynote {
    padding: 0 30px 30px;
    font-size: 30px;
    line-height: 40px;
  }

  .modal-error__button-ok {
    width: 272px;
  }
}
